import React from 'react';

const EventCard = ({ event }) => {
    // Helper to determine color for "Actual"
    const getActualColor = () => {
        if (!event.actual || !event.forecast) return 'text-white';

        // Clean strings to numbers for comparison
        const cleanNumber = (str) => parseFloat(str.replace(/[^0-9.-]/g, ''));
        const actualVal = cleanNumber(event.actual);
        const forecastVal = cleanNumber(event.forecast);

        if (isNaN(actualVal) || isNaN(forecastVal)) return 'text-white';

        // Simple logic: Green if Actual >= Forecast, Red if <. 
        // Ideally this depends on the specific event (e.g. unemployment vs GDP),
        // but per requirements "Actual t·ªët th√¨ Xanh", assuming higher is better for generic request context.
        // For a more robust app, we'd need an 'impact_type' field (e.g., 'bullish_if_higher').

        return actualVal >= forecastVal ? 'text-green-500' : 'text-red-500';
    };

    const formatTime = (isoString) => {
        if (!isoString) return '';
        const date = new Date(isoString);
        return date.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });
    };

    return (
        <div className="border border-gray-700 bg-[#111] p-4 rounded-lg flex flex-col md:flex-row gap-4 hover:border-theme-yellow transition-colors mb-4">
            {/* Left Column: Time + Name */}
            <div className="md:w-1/4 flex flex-col gap-1 border-b md:border-b-0 md:border-r border-gray-800 pb-2 md:pb-0 pr-0 md:pr-4">
                <span className="text-theme-yellow font-bold text-xl">{formatTime(event.event_time)}</span>
                <h3 className="text-white font-semibold text-lg">{event.event_name}</h3>
                <span className="text-xs text-gray-500">{event.currency} ‚Ä¢ {event.impact_level}</span>
            </div>

            {/* Center Column: Indicators */}
            <div className="md:w-1/4 flex flex-col justify-center items-center gap-2 border-b md:border-b-0 md:border-r border-gray-800 pb-2 md:pb-0 pr-0 md:pr-4">
                <div className="grid grid-cols-3 gap-4 text-center w-full">
                    <div className="flex flex-col">
                        <span className="text-xs text-gray-500">Prev</span>
                        <span className="text-gray-300 font-mono">{event.previous || '-'}</span>
                    </div>
                    <div className="flex flex-col">
                        <span className="text-xs text-gray-500">Fcst</span>
                        <span className="text-gray-300 font-mono">{event.forecast || '-'}</span>
                    </div>
                    <div className="flex flex-col">
                        <span className="text-xs text-gray-500">Actual</span>
                        <span className={`font-mono font-bold ${getActualColor()}`}>{event.actual || '--'}</span>
                    </div>
                </div>
            </div>

            {/* Right Column: AI Commentary (Owl's Prophecy) */}
            <div className="md:w-1/2 flex flex-col justify-center gap-4">
                <div className="flex flex-row items-center gap-4">
                    {/* Mascot Display */}
                    {event.ai_sentiment === 'BULLISH' && (
                        <div className="animate-bounce">
                            <img src="/bimbip_bullish.png" alt="B√¨m B·ªãp Bullish" className="w-16 h-16 object-contain" />
                        </div>
                    )}
                    {event.ai_sentiment === 'BEARISH' && (
                        <div className="animate-bounce">
                            <img src="/chimlon_bearish.png" alt="Chim L·ª£n Bearish" className="w-16 h-16 object-contain" />
                        </div>
                    )}

                    {/* Commentary */}
                    <div className="flex items-start gap-2 flex-1">
                        {!event.ai_sentiment && <span className="text-2xl" role="img" aria-label="owl">ü¶â</span>}
                        <div className="text-sm text-gray-300 italic">
                            {event.ai_commentary ? (
                                `"${event.ai_commentary}"`
                            ) : (
                                <span className="text-gray-600">ƒêang ch·ªù C√∫ Th√¥ng Th√°i suy ng·∫´m...</span>
                            )}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default EventCard;
